.message-triggers-wrapper {
  max-width: 100%;
  overflow-x: scroll;
}

.message-trigger {
  flex: 0 0 16rem;
  height: 9rem;
  overflow: hidden;

  & + & {
    margin-left: $spacer-sm;
  }

  .card-body {
    @include border-radius(3px);
    border: 1px solid $black-divider-solid;
  }

  &:hover .card-body {
    border: 1px solid $theme-violet-3;
  }

  .message-title {
    margin-bottom: $spacer-sm;
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-weight: $font-weight-bold;
  }

  .message-caption {
    margin-bottom: $spacer-sm;
    display: block;
    max-height: $spacer-xl;
    min-height: $spacer-xl;
    height: $spacer-xl;
    overflow: hidden;
    font-size: 0.75rem;
    line-height: $spacer;
  }

  .message-meta {
    margin-bottom: 0;
    display: flex;
    justify-content: space-between;
  }

  .message-sender {
    margin: 0;
    font-size: 0.75rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    flex: 0 1 auto;
  }

  .message-timeago {
    margin: 0;
    font-size: 0.75rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    flex: 1 0 auto;
    text-align: right;
  }

  .message-label {
    display: block;
    width: 100%;
    font-size: 0.75rem;
  }
}

.message-article-wrapper {
  position: fixed;
  bottom: 0;
  right: 0;
  z-index: 200;
}

.message-article {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 24rem;
  height: 30rem;

  .marticle-card {
    padding: 0;
  }

  .marticle-head {
    position: absolute;
    top: 0;
    left: 0;
    height: $spacer * 2;
    width: 100%;
    display: flex;
    align-items: center;
    background-color: $white;
    @include faux-border-bottom;
  }

  .marticle-body {
    position: absolute;
    top: 2rem;
    bottom: 2rem;
    left: 0;
    right: 0;
    overflow: hidden;
  }

  .marticle-body-scroll {
    width: 100%;
    height: 100%;
    overflow: scroll;
    padding: $spacer-sm $spacer;
  }

  .marticle-foot {
    position: absolute;
    bottom: 0;
    left: 0;
    min-height: $spacer * 2;
    width: 100%;
    display: flex;
    align-items: center;
    background-color: $white;
    color: $black;
    @include faux-border-top;
  }

  .marticle-action-wrapper {
    display: flex;
  }

  .marticle-action {
    flex: 0 0 auto;
    display: flex;
    height: $spacer-lg;
    padding: $spacer / 4;
    margin: 0 ($spacer / 4);
    color: $theme-gray-6;
    &:hover:not(.active) {
      cursor: pointer;
      color: $theme-gray-8;
    }
  }

  // seriously -___-
  *[data-feather] {
    display: none;
  }

  .marticle-title {
    width: 100%;
    font-weight: $font-weight-bold;
  }
}